import { Field, Form, Formik } from "formik";
import React from "react";
import CheckoutInput from './CheckoutInputs';

const CheckoutForm = () => {
    return (
        <Formik
            initialValues={{
                email: '',
                password: '',
                rememberCheck: false,
            }}

            validate={values => {
                const errors = {};

                if (!values.email) {
                    errors.email = 'اجباری';
                } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
                    errors.email = 'ایمیل اشتباه';
                }

                if (!values.password) {
                    errors.password = 'اجباری';
                } else if (values.password.length < 6) {
                    errors.password = 'حداقل 6 کاراکتر';
                }

                return errors;
            }}

            onSubmit={(values, { setSubmitting }) => {
                setTimeout(() => {
                    alert(JSON.stringify(values, null, 2));
                    setSubmitting(false);
                }, 400);
            }}
        >
            <Form>
                <div className='form-row'>
                    <CheckoutInput
                        name="name"
                        placeholder="Name"
                        type="text"
                    />
                    <CheckoutInput
                        name="lastName"
                        placeholder="Last Name"
                        type="text"
                    />
                </div>
                <div className="submit-row pt-lg-5 m-lg-5 m-0 p-0">
                    <button type="submit" className="btn mt-lg-5">ورود</button>
                </div>
            </Form>
        </Formik>
    );
}

export default CheckoutForm;